var id=0;
var shopid;
export default({
	 
	data:function(){
		return {
			pageLoad:false,
			pageData:[],
			content:""
		}
	},
	onLoad:function(ops){
		this.shopid=ops.shopid;
		this.id=ops.id;
		this.getPage();
	},
	methods:{
		getPage:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/module.php?m=b2b_guest&a=b2b&ajax=1",
				data:{
					shopid:shopid,
					id:id
				},
				dataType:"json",
				success:function(res){
					that.pageLoad=true;
					that.pageData=res.data
					console.log(res.data);
				}
			})
		},
		submit:function(){
			var that=this;
			this.app.post({
				url:this.app.apiHost+"/module.php?m=b2b_guest&a=save&ajax=1",
				data:{
					content:that.content,
					shopid:shopid,
					id:id
				},
				success:function(res){
					that.getPage();
				}
			})
		}
	}
})
